import request from '@/utils/request'

// 
export function platformlogin(data) {
  return request({
    url: `/user/business/platformlogin`,
    method: 'POST',
    data
  })
}

// 
export function getShopInfoById(data) {
  return request({
    url: `/sys/business/shopInfo/getShopInfoById?shopId=${data.shopId}`,
    method: 'GET'
  })
}